<template>
	<div ref="messageContainer" class="message-container">
		<MessageArea title="留言板" :subTitle="`(${messageList.total})`" :isListLoading="isLoading"
			:list="messageList.rows" @submit="handleSubmit" />
	</div>
</template>

<script>
	import * as msgApi from "@/api/message"
	import MessageArea from "@/components/MessageArea"
	import mainScroll from "@/mixin/mainScroll"
	export default {
		data() {
			return {
				isLoading: false,
				messageList: [],
				page: 1,
				limit: 10
			}
		},
		mixins : [mainScroll("messageContainer")],
		components: {
			MessageArea
		},
		computed : {
			// 是否还有多的数据
			isMoreData() {
				// 用的什么方式？
				return this.messageList.rows.length < this.messageList.total
			}
		},
		methods: {
			// 获取数据
			async getMessages() {
				this.isLoading = true
				this.messageList = await msgApi.getMessages(this.page, this.limit)
				this.isLoading = false
			},
			// 处理提交事件
			async handleSubmit(data, callback) {
				const resp = await msgApi.postMessage(data)
				callback("感谢您的留言")
				// 将评论成功的数据加进list中
				this.messageList.rows.unshift(resp)
				this.messageList.total++
			},
			// 监听滚动条
			handleScroll(dom) {
				if(this.isLoading || !dom ){
					// 正在加载
					return	
				}
				const range = 100
				const dec = Math.abs(dom.scrollTop + dom.clientHeight - dom.scrollHeight)
				if( dec <= range ) {
					// 就表示已经到底部了
					this.loadingMore()
				}
			},
			// 加载更多方法
			async loadingMore(){
				if(!this.isMoreData) {
					// 检测是否更多
					return 
				}
				this.isLoading = true
				this.page++
				const resp = await msgApi.getMessages(this.page, this.limit)
				this.messageList.total = resp.total	// ？？？
				this.messageList.rows = this.messageList.rows.concat(resp.rows)
				this.isLoading = false
			},
		},
		created() {
			this.getMessages()
			this.$bus.$on("mainScroll", this.handleScroll)
		},
		destroyed() {
			this.$bus.$off("mainScroll", this.handleScroll)
		},
	}
</script>

<style scoped lang="less">
	.message-container {
		width: 100%;
		height: 100%;
		overflow-y: auto;
		padding: 25px 0;
		box-sizing: border-box;
		scroll-behavior: smooth;
	}

	.message-area-container {
		width: 700px;
		margin: 0 auto;
	}
</style>
