// 导入mock.js数据
import banner from "./banner"
import getBlogCategories from "./getBlogCategories"
import blog from "./blog"
import getBlog from "./getBlog"
import postComment from "./postComment"
import getComments from "./getComments"
import Mock from "mockjs"
import qs from "querystring"
import setting from "./setting"
import getAbout from "./getAbout"
import getProject from "./getProject"
import getMessage from "./getMessage"
import postMessage from "./postMessage"

// 这里定义mock的接口，我以前是这么写的，我照搬
//那这个是正则？嗯一样的—— 噢，就是说还以为只能正则，我在屡屡
Mock.mock("/api/banner", "get", banner)

Mock.mock("/api/getBlogCategories","get",getBlogCategories)

Mock.mock(/^\/api\/blog(\?.+)?$/, "get", blog)

Mock.mock(/^\/api\/blog\/[^/]+$/, "get", getBlog)

Mock.mock("/api/comment", "post", postComment)

Mock.mock(/^\/api\/comment\/?(\?.+)?$/, "get", getComments)

Mock.mock("/api/setting", "get", setting)

Mock.mock("/api/about", "get", getAbout)

Mock.mock(/^\/api\/project\/?$/, "get", getProject)

Mock.mock("/api/message", "post", postMessage)

Mock.mock(/^\/api\/message\/?(\?.+)?$/, "get", getMessage)

Mock.setup({
	timeout : "500-1000"
})
