<template>
	<div class="blog-toc-container">
		<h2>目录</h2>
		<RightList :list="tocWithSelect" @select="handleSelect" />
	</div>
</template>

<script>
	import RightList from "@/views/Blog/components/RightList"
	import { debounce } from "@/util"
	export default {
		components : {
			RightList
		},
		props : {
			toc : {
				type : Array
			}
		},
		data() {
			return {
				activeAnchor : "",
				
			}
		},
		created() {
			this.setSelectDebounce = debounce(this.setSelect, 50)
			this.$bus.$on("mainScroll", this.setSelectDebounce)
		},
		destroyed() {
			this.$bus.$off("mainScroll", this.setSelectDebounce)
		},
		methods : {
			handleSelect(item) {
				// 改动地址栏，使其移动到某个点，并且点亮当前点击的列表点
				// console.log(item);	
				location.hash = item.anchor	//利用哈希的#进行当前页面内容跳转
			},
			// 根据点击的item设置isSelect
			setSelect(scrollDom) {
				if( !scrollDom ) {
					return 
				}
				this.activeAnchor = ""	//方便后面设置
				const range = 200
				// 循环doms
				for(const dom of this.doms) {
					// 看一下当前dom元素是不是应该被选中
					if( !dom ) {
						continue
					}
					// 得到元素离视口顶部的距离
					const top = dom.getBoundingClientRect().top
					if(top >= 0 && top <= range){
						// 规定的范围内 —— 一定选中
						this.activeAnchor = dom.id
						return 
					}else if(top > range){
						//规定的范围下方 —— 一定不激活
						return 
					}else{
						// 规定的范围上方
						this.activeAnchor = dom.id
					}
				}
			}
		},
		computed : {
			// 根据toc得到他们对应的元素数组
			doms() {
				const doms = []
				// 往数组里加东西
				const addTocDoms = (toc) => {
					for(const t of toc) {
						doms.push(document.getElementById(t.anchor))
						if(t.children && t.children.length) {
							addTocDoms(t.children)
						}
					}
				}
				addTocDoms(this.toc)
				return doms
			},
			// ★【递归】根据toc属性以及activeAnchor生成【获得】带有isSelect属性的toc数组
			tocWithSelect() {
				const getToc = (toc = []) => {
					return toc.map((t) => ({
						...t,
						isSelect : t.anchor === this.activeAnchor,
						children : getToc(t.children)
					}))
				}
				return getToc(this.toc)
			}
		}
	}
</script>

<style scoped lang="less">
	.blog-toc-container {

		h2 {
			font-weight: bold;
			letter-spacing: 2px;
			font-size: 1em;
			margin: 0;
		}
	}
</style>
