<template>
	<div v-loading="loading" class="home-container" ref="container" @wheel="handleWheel">
		<!-- @wheel —— 鼠标滚轮事件 -->
		<ul 
		class="carousel-container"
		:style="{ marginTop }"			
		@transitionend="handleTransitionEnd">	<!-- :style="{marginTop}"该style用于控制轮播图移动 -->
			<li v-for="item in data" :key="item.id">
				<Carouselitem :carousel="item" />
			</li>
		</ul>
		<div v-show="index>=1" @click="switchTo(index -1)" class="icon icon-up">
			<Icon type="arrowUp" />
		</div>
		<div v-show="index<data.length-1" @click="switchTo(index + 1)" class="icon icon-down">
			<Icon type="arrowDown" />
		</div>
		<ul class="indicator">
			<li v-for="(item,i) in data" :key="item.id" :class="{active: i === index}" @click="switchTo(i)"></li>
		</ul>
		<!-- <Loading v-if="isLoading"/> -->
	</div>
</template>
<script>
	// import fetchData from "@/mixin/fetchData"
	import { mapState } from "vuex"
	import Icon from "@/components/Icon"
	import Carouselitem from "./Carouselitem.vue"
	import { getBanners } from "@/api/banner"
	export default {
		// mixins : [fetchData]
		components : {
			// Loading,
			Carouselitem,
			Icon
		},
		data() {
			return {
				isLoading : true,	//是否正在加载
				banners : [],
				index : 0,	//当前显示的是第几张轮播图
				containerHeight : 0,	//容器高度A
				switching : false,	//是否正在切换页面，false —— 正在切换
			}
		},
		mounted() {	//获取DOM元素 
			this.containerHeight = this.$refs.container.clientHeight
			// 监听 
			window.addEventListener("resize",this.handleResize)
		},
		destroyed(){
			window.removeEventListener("resize",this.handleResize)
		},
		computed : {
			marginTop() {
				return -this.index * this.containerHeight + "px"
			},
			// 数据仓库 —— 拿出数据
			...mapState("banner", ["loading", "data"])
		},
		methods: {
			// async fechData(){
			// 	return await getBanners()
			// },
			// 自定义函数中单独处理
			// 不是，就是你给我改下来的这套的想法是啥样的呢，这个吗》？
			async getBanner() {
				this.banners = await getBanners()
				// 数据到达后取消Loading状态
				this.isLoading = false
			},
			// 切换轮播图
			switchTo(i){
				this.index = i
			},
			handleResize(){
				this.containerHeight = this.$refs.container.clientHeight
			},
			handleWheel(e) {
				var deltaY = e.deltaY
				if(this.switching){
					return;
				}
				this.switching = true
				if(deltaY === 125){
					// 向下滚轮
					if(this.index < this.banners.length - 1){
						this.index++
					}
					this.switching = false
				}
				if(deltaY === -125){
					// 向上滚轮
					if(this.index != 0){
						this.index--
					}
					this.switching = false
				}
			},
			handleTransitionEnd(){
				this.switching = false
			}
		},
		created() {
			// 不建议在created函数上加async
			this.getBanner();
			// 数据仓库 —— 拿banner的数据
			this.$store.dispatch("banner/fetchBanner")
		}
	}
</script>


<style scoped lang="less">
	@import "~@/styles/mixin.less";
	@import "~@/styles/var.less";
	.home-container{
		width: 100%;
		height: 100%;
		position: relative;
		overflow : hidden;
		// background: @dark;
		
		ul{
			margin: 0;
			list-style: none;
			padding: 0;
		}
	}
	.carousel-container{
		width: 100%;
		height: 100%;
		transition:	500ms;	//轮播图切换事件
		li{
			width: 100%;
			height: 100%;
		}
	}
	.icon {
		.self-center();
		font-size: 30px;
		color: @gray;
		cursor: pointer;
		transform: translateX(-50%);
		@gap : 15px;
		&.icon-up {
			top : @gap;
			animation: jump-up 2s infinite;
		}
		&.icon-down{
			bottom : @gap;
			top : 90%;
			animation: jump-down 2s infinite;
		}
		@jump : 5px;
		// 制作动画
		@keyframes jump-up {
			0% {
				transform: translate(-50%,@jump);
			}
			50% {
				transform: translate(-50%,-@jump);
			}
			100% {
				transform: translate(-50%,@jump);
			}
		}
		@keyframes jump-down {
			0% {
				transform: translate(-50%,-@jump);
			}
			50% {
				transform: translate(-50%,@jump);
			}
			100% {
				transform: translate(-50%,-@jump);
			}
		}
	}
	.indicator {
		.self-center();
		transform: translateY(-50%);	
		left: auto;
		right: 20px;
		li {
			width: 7px;
			height: $width;
			background: @words;
			border-radius: 50%;
			cursor: pointer;
			margin-bottom: 10px;
			border: 1px solid #fff;
			box-sizing: border-box;
			transition: .5s;
			&.active{
				background: #fff;
			}
		}
	}
</style>