<template>
	<div class="blog-category-container">
		<h2>文章分类</h2>
		<RightList :list="list" @select="handleSelect" />
	</div>
</template>	

<script>
	import RightList from "./RightList"
	import fetchData from "@/mixin/fetchData"
	import { getBlogCategories } from "@/api/blog"
	export default {
		// mixins : [fetchData([])],
		data() {
			return {
				blogCategories : []
			}
		},
		components : {
			RightList
		},
		computed : {
			categoryId() {
				return +this.$route.params.categoryId || -1
			},
			limit() {
				return +this.$route.query.limit || 10
			},
			list() { 
				// ?
				const totalArticleCount = this.blogCategories.reduce(
					(a, b) => a + b.articleCount,
					0
				)
				// ?
				const result = [
					{
						name : "全部",
						id : -1,
						articleCount : totalArticleCount
					},
					...this.blogCategories
				]
				// ?
				return result.map((it) => ({
					...it,
					isSelect : it.id === this.categoryId,
					aside : `${it.articleCount}篇`
				}))
			}
		},
		created() {
			// 请求接口
			this.getBlogCategories()
		},
		methods : {
			async getBlogCategories() {
				this.blogCategories = await getBlogCategories()
				// console.log(this.blogCategories)
			},
				// async fetchData() {
				// 	return await getBlogCategories();
				// },
			handleSelect(item){
				// 切换分类肯定到第一页去
				const query = {
					page : 1,
					limit : this.limit
				}
				console.log(this.$route.params.categoryId)
				// 切换路由操作
				if(item.id === -1) {	//到全部分类去
					this.$router.push({
						name : "Blog",
						query
					})
				}else {
					this.$router.push({
						name : "CategoryBlog",
						query,
						params : {
							categoryId : item.id
						}
					})
				}
			}
		}
	}
</script>

<style scoped lang="less">
	.blog-category-container{
		width: 300px;
		box-sizing: border-box;
		padding: 20px;
		position: relative;
		height: 100%;
		overflow-y: auto;
		h2{
			font-weight: bold;
			letter-spacing: 2px;
			font-size: 1em;
			margin: 0;
		}
	}
</style>