<template>
	<ul class="contact-container-ul">
		<li v-for="item in items" :key="item.id">
			<a>
				<div class="icon">
					<Icon :type="item.icon" />
				</div>
				<a v-if="item.icon == 'email'" href="mailto:hzy381798094@163.com">{{ item.title }}</a>
				<a v-else>{{ item.title }}</a>
			</a>
			<div v-if="item.isNeedPop" class="pop">
				<img v-if="item.icon == 'qq'" src="@/assets/qqScan.jpg" alt="" />
				<img v-else-if="item.icon == 'wechat'" src="@/assets/wxScan.jpg" alt="" />
			</div>
		</li>
	</ul>
</template>

<script>
	import { mapState } from "vuex"
	import Icon from "@/components/Icon/index.vue"
	export default {
		components : {
			Icon
		},
		computed : mapState("setting", ["data"]),
		data(){
			return {
				items : [
					{
						id : 1,
						icon : "github",
						title : "Hxs",
						isNeedPop : false
					},
					{
						id : 2,
						icon : "email",
						title : "hzy381798094@163.com",
						isNeedPop : false
					},
					{
						id : 3,
						icon : "qq",
						title : "381798094",
						isNeedPop : true
					},
					{
						id : 4,
						icon : "wechat",
						title : "17361042125",
						isNeedPop : true
					}
				]
			}
		}
	}
</script>

<style scoped lang="less">
	@import "../../styles/global.less";
	@import "../../styles/var.less";
	.contact-container-ul{
		list-style: none;
		padding: 20px;
		color: @gray;
		@itemHeight : 30px;
		li{
			position: relative;
			height: @itemHeight;
			line-height: 30px;
			cursor: pointer;
			margin: 16px 0;
			a{
				display: flex;
				text-align: center;
				.icon{
					font-size: 26px;
					width: 36px;
				}
				&:hover{
					color: #fff;
				}
				a{
					font-size: 14px;
				}
			}
			&:hover{
				.pop{
					transform: scaleY(1);
					transition: 0.2s;
				}
			}
		}
		.pop{
			position: absolute;
			left : 0;
			bottom: @itemHeight + 5px;
			padding: 10px 20px;
			border-radius: 5px;
			background: #fff;
			transform: scaleY(0);
			transform-origin: center bottom;
			img{
				width: 100px;
				height: $width;
			}
			&::after{
				content: "";
				position: absolute;
				left: 46%;
				bottom: -($width/2);
				width: 8px;
				height: $width;
				background: #fff;
				transform: rotate(45deg);
			}
		}
	}
</style>