<!-- 模板 -->
<template>
	<img 
	class="avatar-container"
	:src="url" 
	:style="{
		width : size+'px',
		height : size+'px',
	}" />
</template>
<!-- 功能 -->
<script>
	export default {
		// props —— 属性值
		props: {
			url : {
				type : String,
				required : true 
			},
			size : {
				type : Number,
				default : 150
			},
			
		}
	}
</script>
<!-- 样式 -->
<style scoped>	/*scoped表示带有作用域*/
	.avatar-container {
		border-radius: 50%;
		object-fit: cover;
		display: block;
	}
</style>