<template>
	<div class="carousel-item-container" ref="container" @mousemove="handleMouseMove" @mouseleave="handleMouseLeave">
		<div class="carousel-img" ref="image" :style="imagePosition">
			<ImageLoader 
			:src="carousel.bigImg"
			:placeholder="carousel.midImg"
			@load="this.showWords"/>	<!-- 等图片清晰后再调用显示文字的方法 -->
			<!-- 需要父组件传给他 -->
		</div>
		<div class="title" ref="title">{{ carousel.title }}</div>
		<div class="desc" ref="desc">{{ carousel.description }}</div>
	</div>
</template>

<script>
	import ImageLoader from "@/components/ImageLoader"
	export default {
		components : {
			ImageLoader
		},
		props : ["carousel"],
		// 向父组件表示需要的属性值
		data(){
			return{
				titleWidth : 0,
				descWidth : 0,
				containerSize : null,	//外层容器尺寸
				innerSize : null,	//里层图片尺寸
				// 鼠标得横纵坐标
				mouseX : 0,
				mouseY : 0,
				
			}
		},
		computed : {
			// 计算图片位置 || 得到图片坐标
			imagePosition(){
				// 多出得宽度和高度
				if(!this.innerSize || !this.containerSize){
					return
				}
				const extraWidth = this.innerSize.width - this.containerSize.width
				const extraHeight = this.innerSize.height - this.containerSize.height
				const left = -extraWidth / this.containerSize.width * this.mouseX
				const top = -extraHeight / this.containerSize.height * this.mouseY
				return {
					transform : `translate(${left}px,${top}px)`
				}
			},
			center(){
				return {
					x : this.containerSize.width / 2,
					y : this.containerSize.height / 2
				}
			}
		},
		mounted() {
			this.titleWidth = this.$refs.title.clientWidth
			this.descWidth = this.$refs.desc.clientWidth
			// this.showWords()
			this.setSize()
			// 初始化鼠标坐标
			this.mouseX = this.center.x
			this.mouseY = this.center.y
			// 监听浏览器的尺寸
			window.addEventListener("resize",this.setSize)
		},
		destroyed() {
			window.removeEventListener("resize",this.setSize)
		},
		methods : {
			// 调用该方法显示文字
			showWords() {
				this.$refs.title.style.opacity = 1;
				this.$refs.title.style.width = 0;
				// 强制让浏览器渲染一次
				this.$refs.title.clientWidth	//重新渲染
				
				this.$refs.title.style.transition = "1s";
				this.$refs.title.style.width = this.titleWidth + "px"
				// ---------desc
				this.$refs.desc.style.opacity = 1;
				this.$refs.desc.style.width = 0;
				// 强制让浏览器渲染一次
				this.$refs.desc.clientWidth	//重新渲染
				
				this.$refs.desc.style.transition = "2s 1s";	//显示动画时间、延迟多少时间开始执行
				this.$refs.desc.style.width = this.descWidth + "px"
			},
			// 设置尺寸
			setSize(){
				this.containerSize = {
					width : this.$refs.container.clientWidth,
					height : this.$refs.container.clientHeight
				}
				this.innerSize = {
					width : this.$refs.image.clientWidth,
					height : this.$refs.image.clientHeight
				}
			},
			// 鼠标移动事件
			handleMouseMove(e) {
				const rect = this.$refs.container.getBoundingClientRect()
				this.mouseX = e.clientX - rect.left
				this.mouseY = e.clientY - rect.top
				
			},
			// 鼠标移开事件
			handleMouseLeave(e){
				this.mouseX = this.center.x
				this.mouseY = this.center.y
			},
		}
	}
</script>

<style scoped lang="less">
	@import "~@/styles/var.less";
	.carousel-item-container {
		// background: @dark;
		width: 100%;
		height: $width;
		color: #fff;
		position: relative;
		overflow: hidden;
	}
	.carousel-img{
		width: 110%;
		height: 110%;
		position: absolute;
		left: 0;
		top: 0;
		transition : .3s;	//预防抖动
	}
	.title,.desc{
		position: absolute;
		left: 30px;
		color: #fff;
		text-shadow: 1px 0 0 rgba(0, 0, 0, 0.5), -1px 0 0 rgba(0, 0, 0, 0.5),0 1px 0 rgba(0, 0, 0, 0.5),0 -1px 0 rgba(0, 0, 0, 0.5);
		white-space: nowrap;
		overflow: hidden;
	}
	.title{
		top: calc(50% - 40px);
		font-size: 2em;
	}
	.desc {
		top: calc(50% + 20px);
		font-size: 1.2em;
		color: lighten(@gray,20%);
	}
</style>