<template>
	<div class="blog-comment-container">
		<MessageArea 
		@submit="handleSubmit"
		title="评论列表" 
		:subTitle="'(' + commentList.total + ')'" 
		:list="commentList.rows"
		:isListLoading="isLoading"/>
	</div>
</template>

<script>
	import MessageArea from "@/components/MessageArea"
	import { getComments,postComment } from "@/api/blog"
	export default {
		data() {
			return {
				isLoading : true,
				commentList : [],
				page : 1,	//记录当前页码
				limit : 10,	//记录当前页面数量
			}
		},
		components : {
			MessageArea
		},
		computed : {
			// 是否还有多的数据
			isMoreData() {
				// 用的什么方式？
				return this.commentList.rows.length < this.commentList.total
			}
		},
		methods : {
			// 加载更多
			handleScroll(dom) {
				if(this.isLoading || !dom ){
					// 正在加载
					return	
				}
				const range = 100
				const dec = Math.abs(dom.scrollTop + dom.clientHeight - dom.scrollHeight)
				if( dec <= range ) {
					// 就表示已经到底部了
					this.loadingMore()
				}
			},
			async getComments() {
				this.commentList = await getComments(this.page, this.limit, this.$route.params.id)
				this.isLoading = false
			},
			async loadingMore(){
				if(!this.isMoreData) {
					return 
				}
				this.isLoading = true
				this.page++
				const resp = await getComments(this.page, this.limit, this.$route.params.id)
				this.commentList.total = resp.total	// ？？？
				this.commentList.rows = this.commentList.rows.concat(resp.rows)
				this.isLoading = false
			},
			async handleSubmit(formData, callback) {
				// 为什么只能用...?
				const resp = await postComment({
					blogId : this.$route.params.id,
					...formData
				})	
				// unshift() ?
				this.commentList.rows.unshift(resp)
				this.commentList.total++
				callback("提交成功")
				// this.$showMessage({
				// 	content : "评论成功",
				// 	type : "success",
				// 	duration : 1000,
				// 	callback : () => { console.log("OK") }
				// })
			}
		},
		created() {
			this.getComments()
			this.$bus.$on("mainScroll", this.handleScroll)
		},
		destroyed() {
			this.$bus.$off("mainScroll", this.handleScroll)
		},
	}
</script>

<style scoped lang="less">
	.blog-comment-container{
		margin: 30px 0;
	}
</style>