import Mock from "mockjs"
import qs from "querystring"
export default function(options) {
	const query = qs.parse(options.url)
	return Mock.mock({
		code: 200,
		msg: "",
		data: {
			"total|2000-3000": 0,
			[`rows|${query.limit || 10}`]: [{
				id: "@guid", //生成复杂的id
				title: "@ctitle(1,50)", //生成中文标题
				description: "@cparagraph(1,10)", //生成中文段落（1-10个句子）
				category: {
					"id|1-10": 0, //随机1-10的id数字
					name: "分类@id" //分类id拼接
				},
				"scanNumber|0-3000": 0,
				"commentNumber|0-300": 30, //生成评论数量
				"thumb|1": [
					"@image(300x250, @color, #fff, @natural)",
					null], //有可能会生成图片，有可能没有
				createDate: `@date('T')` //生成时间戳
			}]
		}
	})
}
