<template>
	<div @click="handleClick" class="to-top-container" v-show="show">
		Top
	</div>
</template>

<script>
	export default {
		data(){
			return {
				show : false
			}
		},
		created() {
			// 监听主区域的滚动
			this.$bus.$on("mainScroll", this.handleScroll)
		},
		destroyed() {
			this.$bus.$off("mainScroll", this.handleScroll)
		},
		methods : {
			handleClick(){
				// 回到顶部
				this.$bus.$emit("setMainScroll", 0)
			},
			handleScroll(dom) {
				if( !dom ){
					this.show = false
					return 
				}
				this.show = dom.scrollTop >= 500
			}
		},
	}
</script>

<style scoped lang="less">
	@import "~@/styles/var.less";
	.to-top-container {
		background: @primary;
		width: 50px;
		height: $width;
		border-radius: 50%;
		position: fixed;
		z-index: 99;
		right: 50px;
		bottom: 25px;
		cursor: pointer;
		line-height: $height;
		text-align: center;
		color: #fff;
	}
</style>