<template>
	<div ref="container" class="blog-list-container">
		<ul>
			<li v-for="item in blogs.rows" :key="item.id">
				<div class="thumb" v-if="item.thumb !== null">
					<router-link
					:to="{
						name : 'BlogDetail',
						params : {
							id : item.id
						}
					}">
						<img 
						v-lazy="item.thumb" 
						:alt="item.title"
						:title="item.title"/>
					</router-link>
				</div>
				<div class="main">
					<router-link
					:to="{
						name : 'BlogDetail',
						params : {
							id : item.id
						}
					}">
						<h2>{{ item.title }}</h2>
					</router-link>
					<div class="aside">
						<span>日期：{{ formateDate(item.createDate) }}</span>
						<span>浏览：{{ item.scanNumber }}</span>
						<span>评论{{ item.commentNumber }}</span>
						<!-- 为什么要这样写？ -->
						<router-link
						:to="{
							name : 'CategoryBlog',
							params : {
								categoryId : item.category.id
							}
						}">{{ item.category.name}}</router-link>
					</div>
					<div class="desc">
						{{ item.description }}。
					</div>
				</div>
			</li>
		</ul>
		<Empty v-if="blogs.rows.length === 0 && !isLoading" />
		<Pager 
		v-if="blogs.total" 
		:current="routeInfo.page" 
		:total="blogs.total" 
		:limit="routeInfo.limit" 
		:visibleNumber="10"
		 @pageChange="handlePageChange"/>
	</div>
</template>

<script>
	import fetchData from "@/mixin/fetchData.js"
	import Pager from "@/components/Pager"
	import { getBlogs } from "@/api/blog"	//导入方法专用
	import { formateDate } from "@/util"
	import Empty from "@/components/Empty"
	export default {
		data(){
			return {
				isLoading : true,	//确实在转
				blogs : [],
				rows : [],
				total : 0,
			}
		},
		mixins : [fetchData({})],
		components : {
			Pager,
			Empty
		},
		mounted() {
			// 监听主区域滚动
			this.$bus.$on("setMainScroll", this.handleSetMainScroll)
			this.$refs.container.addEventListener("scroll", this.handleScroll)
		},
		beforeDestroy() {
			this.$bus.$emit("mainScroll")
			this.$refs.container.removeEventListener("scroll", this.handleScroll)
			this.$bus.$off("setMainScroll", this.handleSetMainScroll)
		},
		methods : {
			// 监听页面滚动
			handleScroll() {
				this.$bus.$emit("mainScroll", this.$refs.container)
			},
			handleSetMainScroll(scrollTop) {
				this.$refs.container.scrollTop = scrollTop
			},
			// 处理页码变化
			handlePageChange(newPage) {
				const query = {
					page : newPage,
					limit : this.routeInfo.limit
				}
				// 1. 改变路由——跳转到当前分类id、当前页面容量、newPage的页码
				if(this.routeInfo.categoryId === -1){
					// 当前无分类
					this.$router.push({
						name : "Blog",
						query
					})
				}else{
					// 有分类
					this.$router.push({
						name : "CategoryBlog",
						query,
						params : {
							categoryId : this.routeInfo.categoryId,
							/*
								categoryId —— router.js中配置的:categoryId
								this.routeInfo.categoryId —— 计算属性中获取的路由信息
							*/
						}
					})
				}
			},
			formateDate,
			async fetchData() {
				return await getBlogs(
				this.routeInfo.page,
				this.routeInfo.limit,
				this.routeInfo.categoryId)
			},
			async getBlogs() {
				this.blogs = await getBlogs()
				this.isLoading = false
			}
		},
		computed : {
			// 获取路由信息
			routeInfo() {
				const categoryId = +this.$route.params.categoryId || -1;	//若为undefined，则值设为-1
				const page = +this.$route.query.page || 1	//undefined	则为1
				const limit = +this.$route.query.limit || 10	//undefined 则为10
				return {
					categoryId,
					page,
					limit
				}
			}
		},
		// 观察数据
		watch : {
			// 改变页面数据
			async $route() {	//持续观察this.$route
				this.isLoading = true
				// 设置滚动高度
				this.$refs.container.scrollTop = 0
				// 重新渲染数据列表
				// console.log(this.$route)
				// this.blogs = await this.fetchData()
				// 重新请求数据，并把获取到的路由信息传过去
				this.blogs = await getBlogs(this.routeInfo.page, this.routeInfo.limit, this.routeInfo.categoryId)
				this.isLoading = false
			}
		},
		created() {
			this.getBlogs()
		}
	}
</script>

<style scoped lang="less">
	@import "~@/styles/var.less";
	.blog-list-container{
		line-height: 1.7;
		position: relative;
		padding: 20px;
		overflow-y: scroll;
		width: 100%;
		height: 100%;
		box-sizing: border-box;
		scroll-behavior: smooth;
		ul {
			list-style: none;
			margin: 0;
			padding: 0;
		}
	}
	li {
		display: flex;
		padding: 15px 0;
		border-bottom: 1px solid @gray;
		.thumb {
			flex: 0 0 auto;
			margin-left: 15px;
			img {
				display: block;
				max-width: 200px;
				border-radius: 5px;
			}
		}
		.main {
			margin-left: 15px;
			flex: 1 1 auto;
			h2 {
				margin: 0;
			}
		}
		.aside {
			font-size: 12px;
			color: @gray;
			span {
				margin-right: 15px;
			}
		}
		.desc {
			margin: 15px 0;
			font-size: 14px;
		}
	}
</style>