<template>
	<div class="image-loader-container">
		<img v-if="!deletePlaceholderImg" class="placeholder-img" :src="placeholder" alt="" />
		<!-- ${} —— 字符串拼接技术 —— ES6 -->
		<img @load="handleLoad" class="ready-img" :src="src" :style="{ opacity : originOpacity, transition : `${duration}ms`}" alt="" />
	</div>
</template>

<script>
	export default {
		props : {
			src : {
				tpye : String,
				required : true
			},
			placeholder : {
				type : String,
				required : true
			},
			duration : {
				type : Number,
				default : 500
			}
		},
		data(){
			return {
				loadingOk : false,	//加载是否完成
				deletePlaceholderImg : false,	//删除预加载之前的图片
			}
		},
		computed : {
			// 原图是否加载完成
			originOpacity(){
				return this.loadingOk ? 1 : 0;
			}
		},
		methods : {
			handleLoad(){
				this.loadingOk = true
				setTimeout(() => {
					this.deletePlaceholderImg = true
					this.$emit("load")	//抛出事件，告知已加载完成
				},this.duration)
			}
		}
	}
</script>

<style scoped lang="less">
	@import "~@/styles/mixin.less";
	.image-loader-container{
		position: relative;
		width: 100%;
		height: 100%;
		overflow: hidden;
		img {
			.full-box();
			object-fit: cover;
		}
		.placeholder-img{
			filter: blur(2vw);
		}
	}
</style>