<template>
	<i class="iconfont icon-container" :class="fontClass"></i>
</template>

<script>
	const setClassMap = {
		home : "icon-zhuye",
		qq : "icon-qq",
		warning : "icon-jinggao",
		info : "icon-xinxi",
		email : "icon-email",
		liu_yan : "icon-liuyan",
		blog : "icon-blog",
		weChat : "icon-sign_wechat",
		github : "icon-github-fill",
		empty : "icon-empty",
		about : "icon-set_about_hov",
		code : "icon-code",
		chat : "icon-liuyan",
		wechat : "icon-sign_wechat",
		arrowDown : "icon-arrow-down",
		arrowUp : "icon-shangjiantou"
	}
	export default {
		props : {
			type : {
				type : String,
				required : true
			}
		},
		computed : {
			// 图标样式
			fontClass() {
				return setClassMap[this.type]
			}
		}
	}
</script>

<style scoped>
	@import "https://at.alicdn.com/t/c/font_4075948_s25ujql5wyr.css?spm=a313x.7781069.1998910419.52&file=font_4075948_s25ujql5wyr.css";
	.iconfont {
		width: inherit;
		height: inherit;
		color : inherit;
		font-size: inherit;
	}
</style>