<template>
	<Layout>
		<template #center>
			<div ref="mainContainer" class="main-container">
				<BlogDetail v-if="blog" :blog="blog" />
				<BlogComment />
			</div>
		</template>
		<template #right>
			<div class="right-container">
				<BlogToc v-if="blog" :toc="blog.toc" />
			</div>
		</template>
	</Layout>
</template>

<script>
	// import fetchData from "@/mixin/fetchData"
	import { getBlog } from "@/api/blog"
	import Layout from "@/components/Layout"
	import blog from "@/mock/blog"
	import BlogDetail from "@/views/Blog/components/BlogDetail"
	import BlogToc from "@/views/Blog/components/BlogToc"
	import BlogComment from "@/views/Blog/components/BlogComment"
	import { titleController } from "@/util"
	export default {
		// mixins : [fetchData(null)]
		components: {
			Layout,
			BlogDetail,
			BlogToc,
			BlogComment
		},
		data() {
			return {
				isLoading: true,
				blog: null
			}
		},
		methods: {
			// async fetchData(){

			// },
			async getBlogs() {
				this.blog = await getBlog(this.$route.params.id)
				if(!this.blog) {
					// 文章不存在
					this.$router.push("/404")
					return 
				}
				titleController.setRouteTitle(this.blog.title)
				// this.isLoading = false
				
			},
			handleSrcoll() {
				this.$bus.$emit("mainScroll", this.$refs.mainContainer)
			},
			// 事件总线监听方法 setMainScroll
			handleSetMainScroll(scrollTop) {
				this.$refs.mainContainer.scrollTop = scrollTop
			}
		},
		created() {
			// 获取数据
			this.getBlogs()
			this.$bus.$on("setMainScroll", this.handleSetMainScroll)
		},
		mounted() {
			// 找到div给他添加scroll事件
			this.$refs.mainContainer.addEventListener("scroll", this.handleSrcoll)
		},
		updated() {
			// 由于重新加载页面时页面还没有dom元素，所以等页面dom元素加载成功后在进行update函数
			// 进行hash值更新，先将hash值清空，等50ms后再将hash值重新赋予
			const hash = location.hash
			location.hash = ""
			setTimeout(() => {
				location.hash = hash
			},50)
		},
		beforeDestroy(){
			// 销毁
			this.$refs.mainContainer.removeEventListener("scroll", this.handleSrcoll)
			this.$bus.$off("setMainScroll", this.handleSetMainScroll)
		}
	}
</script>

<style scoped lang="less">
	.main-container {
		overflow-y: scroll;
		overflow-x: hidden;
		scroll-behavior: smooth;
		width: 100%;
		height: 100%;
		box-sizing: border-box;
		padding: 20px;
		position: relative;
	}
	.right-container {
		width: 300px;
		height: 100%;
		overflow-y: scroll;
		box-sizing: border-box;
		position: relative;
		padding: 20px;
	}
</style>
