<template>
	<!-- 总页数大于1 -->
	<div class="pager-container" v-if="pageNumber > 1">
		<a @click="handleClick(1)" 
		:class="{disable : current == 1}">|&lt;&lt;</a>
		<a @click="handleClick(current-1)" 
		:class="{disable : current == 1}">&lt;</a>
		<a @click="handleClick(n)" 
		:class="{active : n === current}" 
		v-for="(n,i) in numbers" 
		:key="i">{{ n }}</a>
		<a @click="handleClick(current+1)" 
		:class="{disable : current == pageNumber}">&gt;</a>
		<a @click="handleClick(pageNumber)" 
		:class="{disable : current == pageNumber}">&gt;&gt;| </a>
	</div>
</template>

<script>
	export default {
		props : {
			current : {
				type : Number,
				default : 1
			},
			total : {
				type : Number,
				default : 0
			},
			limit : {
				type : Number,
				default : 10
			},
			visibleNumber : {
				type : Number,
				default : 10
			}
		},
		methods : {
			handleClick(pageNumber){
				if(pageNumber < 1){
					pageNumber = 1
				}
				if(pageNumber > this.pageNumber){
					pageNumber = this.pageNumber
				}
				if(pageNumber === this.current){
					return
				}
				// 抛出事件
				this.$emit("pageChange",pageNumber)
			}
		},
		computed : {
			// 总页数
			pageNumber(){
				return Math.ceil(this.total / this.limit)
			},
			// 显示的最小的数字
			visibleMinNumber(){
				let min = this.current - Math.floor(this.visibleNumber / 2);
				if(min < 1){
					min = 1  
				}
				return min
			},
			visibleMaxNumber(){
				let max = this.current + this.visibleNumber - 5;
				if(max > this.pageNumber){
					max = this.pageNumber
				}
				return max
			},
			numbers(){
				let nums = []
				for(let i = this.visibleMinNumber; i <= this.visibleMaxNumber; i++){
					nums.push(i)
				}
				return nums
			}
		}
	}
</script>

<style lang="less" scoped>
	@import "~@/styles/var.less";
	.pager-container{
		display: flex;
		justify-content: center;
		margin: 20px 0;
		a{
			cursor: pointer;
			color: @primary;
			margin: 0 6px;
			&.disable{
				color : @lightWords;
				cursor: not-allowed; 
			}
			&.active{
				color :@words;
				font-weight: bold;
				cursor: text;
			}
		}
	}
</style>