<template>
	<form id="data-form-container" @submit.prevent="handleSubmit" class="data-form-container">
		<!-- .prevent —— 修饰符，表示阻止表单的默认行为【例如回车提交】 -->
		<div class="form-item">
			<div class="input-area">
				<input maxlength="10" v-model="formData.nickname" type="text" placeholder="用户昵称" />
				<span class="tip">{{ formData.nickname.length }}/10</span>
			</div>
			<div class="error">{{ error.nickname }}</div>
		</div>
		<div class="form-item">
			<div class="text-area">
				<textarea maxlength="300" v-model="formData.content" placeholder="输入内容"></textarea>
				<span class="tip">{{ formData.content.length }}/300</span>
			</div>
			<div class="error">{{ error.content }}</div>
		</div>
		<div class="form-item">
			<div class="button-area">
				<button :disabled="isSubmiting">
					{{ isSubmiting ? "提交中..." : "提交" }}
				</button>
			</div>
		</div>
	</form>
</template>

<script>
	export default {
		data() {
			return {
				formData : {
					nickname : "",
					content : ""
				},
				error : {
					nickname : "",
					content : ""
				},
				isSubmiting : false
			}
		},
		methods : {
			// 表单提交事件
			handleSubmit() {
				// 根据用户所输入的值去设置error的值
				this.error.nickname = this.formData.nickname ? "" : "请填写昵称"
				this.error.content = this.formData.content ? "" : "请填写内容"
				// 若两个error都有值则无法提交
				if(this.error.nickname || this.error.content) {
					return 
				}
				// error无值则可以直接提交
				this.isSubmiting = true	//防重复点击
				//抛出事件，并且等待回调函数
				this.$emit("submit", this.formData, (success) => {
					// 处理和修改用户提交后的ui交互功能以及恢复其他数据
					this.isSubmiting = false
					this.formData.nickname = ""
					this.formData.content = ""
				})	
			}
		}
	}
</script>

<style scoped lang="less">
	@import "~@/styles/var.less";
	.data-form-container {
		margin-bottom: 20px;
		overflow: hidden;
	}
	.form-item {
		margin-bottom: 8px;
	}
	.input-area {
		width: 50%;
		position: relative;
	}
	.text-area {
		position: relative;
	}
	.tip {
		position: absolute;
		right: 5px;
		bottom: 5px;
		color: #b4b8bc;
		font-size: 12px;
	}
	input,
	textarea {
		display: block;
		width: 100%;
		box-sizing: border-box;
		border: 1px dashed @gray;	//虚线
		outline: none;
		color: @words;
		font-size: 14px;
		border-radius: 4px;
		&:focus {
			border-color: @primary;
		}
	}
	input {
		padding: 0 15px;
		height: 40px;
	}
	textarea {
		resize: none;	//	根据需要在哪个方向上调整元素大小
		padding: 8px 15px;
		height: 120px;
	}
	.error {
		margin-top: 6px;
		color: @danger;
		font-size: 14px;
		height: 20px;
		line-height: 20px;
	}
	button {
		position: relative;
		cursor: pointer;
		border: none;
		outline: none;
		width: 100px;
		height: 34px;
		color: #fff;
		border-radius: 4px;
		background: @primary;
		&:hover {
			background: darken(@primary, 10%);
		}
		&:disabled {
			background: lighten(@primary, 20%);
			cursor: not-allowed;
		}
	}
</style>