export default function (timestamp, showTime=false) {	
	const date = new Date(+timestamp)	//将timestamp转化成数字
	const month = (date.getMonth() + 1).toString().padStart(2,"0")
	const day = (date.getDate()).toString().padStart(2,"0")
	// 得到月和日，并且字符串化	padStart(字符串长度,"用于补全的字符") —— 需要补全字符串长度，若长度不够：start从前面补,end从后面补
	let str = `${date.getFullYear()}-${month}-${day}`
	if(showTime){
		const hour = date.getHours().toString().padStart(2,"0")
		const minite = date.getMinutes().toString().padStart(2,"0")
		const second = date.getSeconds().toString().padStart(2,"0")
		str += ` ${hour}:${minite}:${second}`
	}
	return str
}