// 设置和导入需要的页面
import Home from "@/views/Home"
import About from "@/views/About"
import Blog from "@/views/Blog/"
import Message from "@/views/Message"
import Project from "@/views/Project"
import BlogDetail from "@/views/Blog/Detail"
import NotFound from "@/views/NotFound"

const routes = [{
		name: "Home",
		path: "/",
		component: Home,
		meta : {
			title : "首页"
		}
	},
	{
		name: "About",
		path: "/about",
		component: About,
		meta : {
			title : "关于我"
		}
	},
	{
		name: "Blog",
		path: "/article",
		component: Blog,
		meta : {
			title : "博客"
		}
	},
	{
		name: "CategoryBlog",
		path: "/article/cate/:categoryId",
		component: Blog,
		meta : {
			title : "博客"
		}
	},
	{
		name: "BlogDetail",
		path: "/article/:id",
		component: BlogDetail,
		meta : {
			title : "博客详情"
		}
	},
	{
		name: "Message",
		path: "/message",
		component: Message,
		meta : {
			title : "留言板"
		}
	},
	{
		name: "Project",
		path: "/project",
		component: Project,
		meta : {
			title : "技术分享"
		}
	},
	{
		name : "NotFound",
		path : "*",	//表示除了以上的路径，其余路径都是匹配该页面
		component : NotFound
	}
]


export default routes
